.wrapper > .container {
  position: relative;
  min-height: 800px;
  z-index: -2;
  padding-top: 40px;
  padding-bottom: 80px;

  @include vp-1199 {
    min-height: 1024px;
  }

  @include vp-767 {
    min-height: 568px;
  }
}

.header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 2;
  padding: 40px 120px 0;

  @include vp-1199 {
    padding: 40px 45px 0;
  }

  @include vp-767 {
    padding: 15px 15px 0;
  }
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include vp-767 {
    display: grid;
    grid-template-columns: 26px 112px;
    align-items: start;
    row-gap: 8px;
  }
}

.header__logo {
  display: flex;

  @include vp-1199 {
    position: absolute;
    left: 0;
    right: 0;
    justify-content: center;
    width: 144px;
    margin: 0 auto;

    svg {
      width: 144px;
      height: 44px;
    }
  }

  @include vp-767 {
    position: relative;
    grid-column: 2/3;
    width: 112px;

    svg {
      width: 112px;
      height: 34px;
    }
  }
}

.header a {
  color: $color-default-white;
}

.header__phone {
  font-size: 18px;
  font-weight: 600;
  line-height: 1;
  transition: color 0.1s ease;

  &:hover {
    color: $color-active;
  }

  &:focus {
    outline: none;
    color: $color-active;
  }

  &:active {
    color: $color-navy-blue;
  }

  @include vp-1199 {
    font-size: 16px;
  }

  @include vp-767 {
    grid-column: 2/3;
    font-size: 14px;
    text-align: right;
  }
}
