  .footer .container > p {
    text-align: start;
  }
}

.footer__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}
@media (max-width: 1199px) {
  .footer__content {
    align-items: flex-start;
    margin-bottom: 38px;
  }
}
@media (max-width: 767px) {
  .footer__content {
    flex-direction: column;
    margin-bottom: 26px;
  }
}

.footer__logo {
  display: flex;
}
@media (max-width: 767px) {
  .footer__logo {
    margin-bottom: 14px;
  }
}

@media (max-width: 1199px) {
  .footer__logo svg {
    width: 118px;
    height: 34px;
  }
}
@media (max-width: 767px) {
  .footer__logo svg {
    width: 112px;
  }
}

.footer__list {
  display: flex;
  gap: 33px;
}
@media (max-width: 1440px) {
  .footer__list {
    padding-right: 65px;
  }
}
@media (max-width: 1340px) {
  .footer__list {
    padding: 0;
  }
}
@media (max-width: 1199px) {
  .footer__list {
    padding-right: 6px;
    gap: 21px;
  }
}
@media (max-width: 767px) {
  .footer__list {
    padding-right: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 5px;
    width: 100%;
  }
}

.footer__list a {
  font-size: 16px;
  line-height: 16px;
  padding: 10px 0;
  position: relative;
}
.footer__list a::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background-color: transparent;
  bottom: 6px;
  transition: background-color 0.2s ease;
}
.footer__list a:hover::before {
  background-color: #2d383f;
}
.footer__list a:focus {
  outline: 1px #2d383f solid;
}
.footer__list a:active::before {
  background-color: transparent;
}
@media (max-width: 1199px) {
  .footer__list a {
    font-size: 14px;
    line-height: 14px;
  }
}

.footer__phone {
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  transition: color 0.1s ease;
}
.footer__phone:hover {
  color: #1c3374;
}
.footer__phone:focus {
  outline: none;
  color: #1c3374;
}
.footer__phone:active {
  color: #0266c1;
}
@media (max-width: 1199px) {
  .footer__phone {
    display: none;
  }
}

.item-1 {
  order: 3;
}
@media (max-width: 767px) {
  .item-1 {
    order: initial;
  }
}

.item-3 {
  order: 4;
}
@media (max-width: 767px) {
  .item-3 {
    order: initial;
  }
}

.item-5 {
  order: 5;
}
@media (max-width: 767px) {
  .item-5 {
    order: initial;
  }
}

.social {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 14px;
}
@media (max-width: 1199px) {
  .social {
    margin-bottom: 12px;
  }
}
@media (max-width: 767px) {
  .social {
    justify-content: flex-start;
    margin-bottom: 13px;
  }
}

.social a:hover rect {
  stroke: #000000;
  opacity: 1;
}

.social a:focus {
  outline: none;
}
.social a:focus rect {
  stroke: #000000;
  opacity: 1;
}

.social a:active svg {
  fill: #0266c1;
}
.social a:active rect {
  stroke: #0266c1;
  opacity: 1;
}
.social a:active path {
  fill: #ffffff;
}

